.home-container {
	padding-top: 8rem;
}

.home-first-area {
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.home-first-area-left-side {
	display: flex;
	flex-direction: column;
	align-items: flex-start;
}

.home-title {
	width: 85% !important;
}

.home-subtitle {
	padding: 0.5rem;
	text-align: left;
	font-family: var(--code-font);
}

.home-first-area-right-side {
	display: flex;
	align-items: center;
}

.home-image-container {
	width: 25rem;
    margin-bottom: 3rem;
}

.home-image-wrapper {
	overflow: hidden;
	border-radius: 2rem;
	transform: rotate(3deg);
	box-shadow: 0px 0.5rem 0.7rem rgba(0, 0, 0, 0.25);
}

.home-image-wrapper img {
	width: 100%;
}

.home-socials {
	display: flex;
	justify-content: left;
	align-items: center;
	padding-top: 2rem;
	padding-bottom: 4rem;
}

.home-social-icon {
	padding-right: 1rem;
	font-size: var(--font-size-large);
	transition: color 0.2s ease-in-out;
	color: var(--secondary-color);
}

.home-social-icon:hover {
	color: var(--primary-color);
}

.home-after-title {
	display: flex;
}

.home-projects {
	display: flex;
	justify-content: center;
	padding-top: 2rem;
}

.home-educations {
	padding-top: 2rem;
}

@media (max-width: 1024px) {
	.home-first-area {
		flex-direction: column;
	}

	.home-first-area-left-side {
		display: flex;
		flex-direction: column;
		align-items: flex-start;
		width: 100%;
		order: 2;
	}

	.home-title {
		width: 100%;
		margin-top: 1rem;
		order: 2;
	}

	.home-subtitle {
		width: 100%;
		margin-top: 1rem;
		order: 3;
	}

	.home-first-area-right-side {
		width: 100%;
		order: 1;
		justify-content: center;
	}

	.home-after-title {
		flex-direction: column;
	}

	.home-projects {
		flex-direction: column;
    	align-items: center;
	}
}
