.educations {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 1rem;
}

.education-card {
    grid-column: span 6;
    display: flex;
    flex-direction: column;
    place-items: center;

	border: 1px solid var(--quaternary-color);
    border-radius: 1rem;
    box-shadow: 0 0.125rem 0.25rem 0 var(--quaternary-color);
    padding: 2rem 1rem 2rem;
    
}

.education-card:hover {
	background: #fafafa;
	transition: background-color 0.3s ease-in-out;
}

.education-card:hover .education-card-link {
	color: var(--link-color);
	transition: color 0.3s ease-in-out;
}

.education-card-header {
	display: flex;
	align-items: center;
	justify-content: flex-end;
	width: 100%;
}

.education-card-icon {
	font-size: var(--font-size-large);
	color: var(--grey-color);
}

.education-body {
	margin-bottom: 1rem;
	width: 100%;
	padding-top: 0.8rem;
	font-size: var(--font-size-large);
}

.education-header {
	display: flex;
    padding-bottom: 0.5rem;
    align-items: center;
}

.education-image {
	height: 2rem;
	width: 2rem;
	border-radius: 50%;
	outline: 0.2rem solid white;
	box-shadow: 0px 0.5rem 0.7rem rgba(0, 0, 0, 0.25);
}

.education-title {
	font-size: var(--font-size);
	font-weight: 700;
	padding-left: 1rem;
	margin-top: -3px;
	color: var(--secondary-color);
	text-decoration: none;
}

.education-subtitle {
	font-size: var(--font-size-small);
    color: var(--secondary-color);
    padding: 0.5rem 3rem 0 3rem;
    text-align: left;
}

.education-duration {
	display: flex;
	align-items: center;
	justify-content: space-between;
	font-size: var(--font-size-small);
	padding: 1rem 3rem 0 3rem;
	color: var(--grey-color);
}

@media (max-width: 50rem) {
    .education-card {
        grid-column: span 12;
    }
}

