
.about-container {
	display: flex;
	flex-direction: column;
	height: 100%;
	margin: 0;
	padding-top: 8rem;
	text-align: left;
}

.about-main {
	display: flex;
}

.about-right-side {
	flex-basis: 150rem;
    padding: 1rem;
}

.about-title {
	width: 100%;
}

.about-subtitle {
	width: 100%;
}

.about-left-side {
	display: flex;
	flex-direction: column;
	flex-basis: 20rem;
}

.about-image-container {
	width: 15rem;
}

.about-image-wrapper {
	display: inline-block;
	overflow: hidden;
	width: 100%;
	border-radius: 10%;
	transform: rotate(3deg);
	box-shadow: 0px 0.5rem 0.7rem rgba(0, 0, 0, 0.25);
}

.about-image-wrapper img {
	display: block;
	width: 100%;
}

.about-socials {
	padding-top: 3rem;
	padding-bottom: 3rem;
}

.about-socials-mobile {
	display: none;
}

@media (max-width: 1024px) {
	.about-logo {
		z-index: 100;
	}

	.about-main {
		flex-direction: column;
	}

	.about-subtitle {
		padding-top: 1.5rem;
	}

	.about-left-side {
		flex-basis: 100%;
		order: 1;
		align-items: center;
	}

	.about-right-side {
		flex-basis: 100%;
		order: 2;
	}

	.about-image-container {
		padding-bottom: 2rem;
	}

	.about-socials {
		display: none;
	}

	.about-socials-mobile {
		padding-top: 2rem;
		display: flex;
	}
}
