.projects-list {
	width: 100%;;
	display: flex;
	flex-wrap: wrap;
	align-items: stretch;
	padding-top: 1.5rem;
	padding-bottom: 1.5rem;
	margin:0;
}

.project {
	width: calc(100% / 3);
	padding: 2rem;
	border-radius: 1rem;
}

.project:hover {
	background: #fafafa;
	transition: background-color 0.3s ease-in-out;
}

.project:hover .project-link {
	color: var(--link-color);
	transition: color 0.3s ease-in-out;
}

.project:hover .project-link .project-link-text{
	color: var(--link-color);
}

.project-logo {
	width: 100%;
    display: flex;
    flex-direction: row;
    gap: 0.3rem;
    flex-wrap: wrap;
}

.project-logo img {
	width: 2rem;
}

.project-title {
	font-family: var(--secondary-font);
	color: var(--primary-color);
	font-size: var(--font-size);
	padding-top: 1rem;
	margin-bottom: 1em;
	font-weight: 700;
}

.project-description {
	text-align: left;
	color: var(--secondary-color);
}
.project-description-item {
	padding-top: 0.5rem;
}

.project-link {
	display: flex;
	align-items: center;
	color: var(--secondary-color);
	font-size: var(--font-size-small);
	padding-top: 1.2rem;
}

.project-link-icon {
	padding-left: 0.3rem;
	font-size: var(--font-sizee-small);
}

.project .project-link-text {
	padding-left: 1.2rem;
	font-weight: 700;

	color: var(--secondary-color);
	text-decoration: none;
}

@media (max-width: 1024px) {
	.project {
		width: calc(100% / 2);
	}
}

@media (max-width: 800px) {
	.project {
		width: calc(100% / 1);
	}
}