.nav {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 999;
    width: 100%;
}

.nav-container {
	display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 4rem;
    padding: 2rem 0rem;
	background: linear-gradient(to bottom, white, transparent);
}

.nav-logo-container {
    position: absolute;
    top: 2rem;
    left: 6rem;
    border: 1px solid white;
	border-radius: 50%;
	box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.25);
}

.nav-logo {
	display: block;
	margin: 0;
	padding: 0;
}

.navbar {
    display: flex;
    justify-content: center;
    align-items: center;
    margin:0 auto;
}

.nav-background {
    background: #fff;
    box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.1);
    border-radius: 3rem;
}

.nav-list {
	display: flex;
    justify-content: space-between;
    list-style: none;
    align-items: center;
    padding: 0.5rem 1rem;
}

.nav-item {
	font-weight: bold;
	font-size: var(--font-size);
    padding: 0.5rem;
}

.nav-item.active a {
	color: var(--link-color) !important;
}

.nav-item a {
	text-decoration: none;
	color: var(--primary-color);
}

.nav-item a:hover {
	color: var(--link-color);
	transition: color 0.3s ease-in-out;
}

@media (max-width: 820px) {
    .nav-container {
        margin: 0 2rem;
    }
    .nav-logo-container {
        left: 3rem;
    }
    .navbar {
        margin: 0;
    }
    .nav-item {
		font-size: var(--font-size-small);
	}
}

@media (max-width: 600px) {
    .nav-list {
        padding: 0.5rem;
    }
    
    .nav-item {
        font-size: var(--font-size-small);
        padding: 0.3rem;
    }
}

@media (max-width: 500px) {
    .nav-container {
        margin: 4rem 0rem ;
    }
}