.resume-container {
	display: flex;
	flex-direction: column;
	height: 100%;
	margin: 0;
	padding-top: 8rem;
}

.resume-file-container {
	display: flex;
    flex-direction: column;
}
.resume-file-btn {
	display: flex;
    justify-content: center;
}

.resume-file-btn .resume-file-btn-link {
	display: flex;
	align-items: center;
	gap: 0.5rem;
    padding: var(--gap-small) var(--gap-large);
    border: 1px solid var(--link-color);
    margin: var(--gap-middle);
	border-radius: 2rem;

    
    color: var(--link-color);

    font-size: var(--font-size);
    line-height: var(--line-height-large);
    text-decoration: none;
    cursor: pointer;
}

.resume-file-btn .resume-file-btn-link:hover{
	color: var(--white-color);
	background-color: var(--link-color);
}
.resume-file {
	width: 100%;
}
